import { loadData } from '@/api/common'
import FootNav from '@/components/foot-nav'
import Scroll from '@/components/scroll'
import Tips from './components/tips'
import { imgUrl } from '@/api/url'
export default {
  components: {
    FootNav,
    Scroll,
    Tips
  },
  data() {
    return {
      imgUrl,
      isShow: true,
      giftList: [],
      parameter: {
        id: ''
      }
    }
  },
  created() {
    this.initDate()
  },
  methods: {
    initDate() {
      loadData('/goods/page', {
        type: 2
      }).then(data => {
        this.giftList = data.data
        console.log(data)
      })
    },
    toGet(id) {
      this.parameter.id = id
      this.$refs.Tips.show()
    }
  }
}



