import { loadData } from '@/api/common'
import ModalBg from '@/components/base/modal-bg'
export default {
  components: {
    ModalBg
  },
  data() {
    return {
      modalShow: false
    }
  },
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    parameter: {
      type: Object
    }
    // clickBgClose: {
    //   type: Boolean,
    //   default: false
    // }
  },
  methods: {
    closeModal() {
      //   if (!this.clickBgClose) {
      //     return false
      //   }
      //   this.modalShow = false
      this.$emit('update:isShow', false)
    },
    affirm() {
      console.log(this.parameter.id)
      loadData('/goods/take/' + this.parameter.id).then(data => {
        console.log(data)
        this.modalShow = false
      })
    },
    show() {
      this.modalShow = true
    },
    hide() {
      this.modalShow = false
    }
  }
}



